
.notUnlocked{
    display: none !important;
}

#GeneratorsfirstRow,
#GeneratorssecondRow{
    height: 38vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

#GeneratorssecondRow{
    height: 38vh;
}

.Generator{
    width: 15vw;
    height: 30vh;
}

.Generator h3{
    font-size: 1.5vw;
}

.Generator h4{
    font-size: 1.0vw;
}

.Generator p{
    font-size: 1.0vw;
}

#fireGenerator:disabled,
#fireGenerator[disabled]{
    background-color: #803141 !important;
}


#fireGenerator{
    background-color: #a43f53;
    border: #53354A solid 2px;
    border-radius: 15px;
}

#fireGenerator:hover{
    background-color: #903749;
}

#waterGenerator:disabled,
#waterGenerator[disabled]{
    background-color: #244E92 !important;
}


#waterGenerator{
    background-color: #326ecd;
    border: #53354A solid 2px;
    border-radius: 15px;
}

#waterGenerator:hover{
    background-color: #2d62b7;
}

#earthGenerator:disabled,
#earthGenerator[disabled]{
    background-color: #7E5B1C !important;
}

#earthGenerator{
    background-color: #9E7323;
    border: #53354A solid 2px;
    border-radius: 15px;
}

#earthGenerator:hover{
    background-color: #8F6820;
}

#airGenerator{
    background-color: #ebebeb;
    border: none;
    border-radius: 15px;
}

#airGenerator:hover{
    background-color: #d4d0cf;
}



#spaceGenerator{
    border: none;
    border-radius: 15px;
    background-color: darkslateblue;
}

#aetherGenerator{
    border: none;
    border-radius: 15px;
    background-color: rgba(218, 165, 32, 0.9);
}

#aetherGenerator:hover{
    background-color: rgba(190, 145, 15, 0.9);
}

#spaceGenerator:hover{
    background-color: #3e3578;
}

#spaceGenerator {
    position: relative;
}

#spaceGenerator::before {
    content: "";
    position: absolute;
    top: -5%;
    left: -5%;
    width: 110%;
    height: 110%;
    background-image: url("data:image/jpeg;base64,");
    background-repeat: repeat;
    border-radius: 25px;
    z-index: -1;
  }

  #aetherGenerator {
    position: relative;
}

#aetherGenerator::before {
    content: "";
    position: absolute;
    top: -5%;
    left: -5%;
    width: 110%;
    height: 110%;
    background-image: url("https://ak9.picdn.net/shutterstock/videos/20441299/thumb/12.jpg?ip=x480");
    border-radius: 25px;



    background-repeat: repeat;
    z-index: -1;
  }

  .notUnlocked{
    display: none !important; 
  }